import React,{Component} from 'react';
import './Districtlist.css';
import Chart from 'react-apexcharts';



class DistrictList extends Component
{
    constructor(props){
        super(props);

        this.state = {
          
            series: [{
              data: [400, 430, 448, 470, 540, 580, 690, 1100, 1200, 1380]
            }],
            options: {
              
            },
          selectOptions:null,
          data:null,
          lables:null,
          
          };
    }
    componentWillMount=()=>{
        let arr =[];
       console.log(this.props.data[0].data.district);
       this.props.data.forEach(element => {
           arr.push(element.name);
       });
       this.formateDate(this.props.data[0].data.district);
       this.setState({
        selectOptions:arr
       })
    }

    formateDate=(data)=>{
        let arr = [];
        let lables=[];
        for (let key in data) {
            if (data.hasOwnProperty(key)) {
                arr.push(data[key].confirmed);
                lables.push(key);
            }
        }
        this.setState({
            data : arr,
            lables:lables
        })
    }

    getDistrict=(e)=>{
        this.props.data.forEach(element=>{
            if(element.name===e.target.value)
            {
                this.formateDate(element.data.district);
                return;
            }
        });
    }

    render()
    {
        return(
            <div className="district_data_back">
                <h3>District Wise Data</h3>
                <select onChange={(e)=>this.getDistrict(e)}>
                    {this.state.selectOptions.map((x)=>
                        <option key={x} value={x}>{x}</option>
                    )
                    }
                </select>
                {this.state.data && this.state.lables && <Chart
                 options={this.state.options} 
                 options={
                     {
                        chart: {
                            type: 'bar',
                            height: 350
                          },
                          plotOptions: {
                            bar: {
                              horizontal: true,
                            }
                          },
                          dataLabels: {
                            enabled: false
                          },
                          xaxis: {
                            categories: this.state.lables,
                          }
                     }
                 }
                 series={
                    [{
                        name:'Confirmed cases',
                        data: this.state.data
                    }]
                 } 
                 type="bar"
                  height={800} />}
            </div>
        );
    }
} 

export default DistrictList;