import React,{Component} from 'react';
import './Statelist.css';
import {HorizontalBar} from 'react-chartjs-2';
import Media from 'react-media';
import DistrictList from '../Districtwise/DistrictList';

class Statelist extends Component{

    constructor(props){
        super(props);
        this.state={
            state_data:null,
        }
    }

    componentWillMount(){
        this.setState({
            state_data:this.props.listData
        })
    }

    render(){
        return(
        <div > 
            <DistrictList data={this.state.state_data}/>
            {this.state.state_data!=null &&
                 <Media query="(max-width:45rem)">
                 {matches => matches ?
                   <div className="mob_view_back">
                           {this.state.state_data.map((x)=>
                                <div className="mob_list_col"  key={x.name}>
                                    <div className="mob_list_view">
                                        <p>State</p>
                                        <p>{x.name}</p>
                                    </div>
                                    <div className="mob_list_view cases">
                                        <p >Confirmed</p>
                                        <p>{x.data.confirmed}</p>
                                    </div>
                                    <div className="recovered mob_list_view">
                                        <p>Recovered</p>
                                        <p>{x.data.recovered}</p>
                                    </div>
                                    <div className="mob_list_view">
                                        <p>Death</p>
                                        <p>{x.data.deaths}</p>
                                    </div>
                                    <div className="mob_list_view cases">
                                        <p>Active</p>
                                        <p>{x.data.active}</p>
                                    </div>
                                </div>)}
                    </div>:
                   
                    <table className="table_background">
                         <thead>
                            <tr>
                                <th>State Name</th>
                                <th className="cases">Confirmed</th>
                                <th className="recovered">Recovered</th>
                                <th>Deaths</th>
                                <th>Active cases</th>
                            </tr>
                         </thead>
                         <tbody >
                            {this.state.state_data.map((x)=>
                              <tr key={x.name}>
                                  <td>{x.name}</td>
                                  <td className="cases">
                                 {x.data.confirmed}   
                                  </td>
                                  <td className="recovered">
                                  {x.data.recovered}
                                  </td>
                                  <td>{x.data.deaths}</td>
                                  <td className="cases">{x.data.active}</td>
                              </tr>)}
                        </tbody>
                    </table>
                 
                
                }
                </Media>
                
            }
        </div>);
    }
}

export default Statelist;