import React,{Component} from 'react';
import './Dashboard.css';
import Statelist from '../Statelist/Statelist.js';
import Overall from '../Overall/Overall.js';
import Timeline from '../Timeline/Timeline.js';
import Loading from '../Loading/Loading.js';


class Dashboard extends Component{

    constructor(props){
        super(props);
        this.state={
            data:[],
            total:null,
            graph:null,
            dist_data:null,
            dist_lables:null
        }
    }

    formateDate=(data)=>{
        let arr = [];
        for (let key in data) {
            if (data.hasOwnProperty(key)) {
                let val = {
                    name : key,
                    data : data[key]
                }
                arr.push(val);
            }
        }
        this.setState({
            data : arr
        })
    }

    
    componentWillMount()
    {
       
        fetch("https://corona-virus-world-and-india-data.p.rapidapi.com/api_india_timeline", {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "corona-virus-world-and-india-data.p.rapidapi.com",
                "x-rapidapi-key": "c3d2b397d1msh4dd458c2bbee8bcp11db17jsnd1422fa45a01"
            }
        })
        .then(response => {
            response.json().then((data)=>{
                console.log(data);
                this.setState({
                    graph:data
                })
            });
        })
        .catch(err => {
            console.log(err);
        });
        
        fetch("https://corona-virus-world-and-india-data.p.rapidapi.com/api_india", {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "corona-virus-world-and-india-data.p.rapidapi.com",
                "x-rapidapi-key": "f40a13c499mshe4c47cfdb937d88p1de137jsn5414b3a88492"
            }
        })
        .then(response => {
            response.json().then((data)=>{
                console.log(data);
                this.formateDate(data.state_wise);  
                this.setState({
                    total:data.total_values
                })
            });
        })
        .catch(err => {
            console.log(err);
        });
    }
    render(){
        return(
            <div>
            {this.state.data.length>0 && this.state.total ?
            <div >
                <div className="home_heading">
                    <p>COVID-19 Cases Overview India</p>
                    <p>{this.state.total.lastupdatedtime}</p>
                </div>
                <div className="front_page_vector_484">
                    <div className="cases_123">
                        <div className="data_J818">
                            <div className="vector_456">
                                <p>Confirmed</p>
                                <img src="https://www.medizkart.com/image/cache/catalog/product-img-1/icons/INFECTION%201-200x200.png"/>
                            </div>
                            <p className="cases">{this.state.total.confirmed}</p>
                        </div>
                        <div className="data_J818">
                            <div className="vector_456">
                                <p>Recovered</p>
                                <img src="https://image.flaticon.com/icons/svg/1513/1513074.svg"/>
                            </div>
                            <div className="recovered count_3443">
                                <p>{this.state.total.recovered}</p>
                                <p>{Math.round((this.state.total.recovered/this.state.total.confirmed)*100)}% of total cases</p>
                            </div>
                        </div>
                        <div className="data_J818">
                            <div className="vector_456">
                                <p>Deaths</p>
                                <img src="https://clipartart.com/images/death-symbol-clipart-1.jpg"/>
                            </div>
                            <div className="cases count_3443">
                                <p>{this.state.total.deaths}</p>
                                <p>{Math.round((this.state.total.deaths/this.state.total.confirmed)*100)}% of total cases</p>
                            </div>
                        </div>
                        <div className="data_J818">
                            <div className="vector_456">
                                <p>Active</p>
                                <img src="https://cdn.onlinewebfonts.com/svg/img_515115.png"/>
                            </div>
                            <div className="count_3443">
                                <p>{this.state.total.active}</p>
                                <p>{Math.round((this.state.total.active/this.state.total.confirmed)*100)}% of total cases</p>
                            </div>
                        </div>
                    </div>
                    <Overall data={this.state.total}/>
                </div>
                <div className="data_stack">
                    {this.state.graph && <Timeline data={this.state.graph}/>}
                    <Statelist listData={this.state.data}/>
                </div>
               
            </div> : <Loading/>}
            </div>
        );
    }
}

export default Dashboard;