import React,{Component} from 'react';
import {Doughnut} from 'react-chartjs-2';
import './Overall.css';

class Overall extends Component{

    constructor(props){
        super(props);
        this.state={
            lables : ['Active','Discharged','Death'],
            dataset:[{
                data : [ this.props.data.active,
                    this.props.data.recovered,
                    this.props.data.deaths],
                backgroundColor : ['red','#66ff99','grey']
            }]
        }
    }

    render(){
        return(<div>
            {this.state.dataset!=null &&
              <div className="pie_background">
                  <h3>Overall breakdown</h3>
                  {
                    <Doughnut 
                    data={{
                        labels:this.state.lables,
                        datasets:this.state.dataset
                    }}
                    options={{
                        responsive:false,
                        maintainAspectRatio:false
                    }}
                    height={300}
                    width={280}
                    />
                }
              </div>
            }
        </div>)
    }
}

export default Overall;