import React,{Component} from "react";
import {Line} from 'react-chartjs-2';
import './Timeline.css';

class Timeline extends Component{
    constructor(props){
        super(props);
        this.state={
            dataset:null,
            lables:null,
            recovered:null
        }
    }

    formateData=(data)=>{
        let arr = [];
        let lables =[];
        let rec=[];
        for (let key in data) {
            if (data.hasOwnProperty(key)) {
                arr.push(parseInt(data[key].totalconfirmed));
                lables.push(data[key].date);
                rec.push(data[key].totalrecovered);
            }
        }
        this.setState({
            dataset:arr,
            lables:lables,
            recovered:rec
        })
    }

    componentWillMount()
    {
        this.formateData(this.props.data);
    }
   
    render()
    {
        return(
        <div className="line_graph_124">
           {this.state.dataset && this.state.lables && this.state.recovered && 

           <Line
               data={{
                   labels:this.state.lables,
                   datasets: [
                    {
                      label: 'Confirmed',
                      type:'bar',
                      fill: false,
                      lineTension: 0.6,
                      backgroundColor: '#FF8C00',
                      borderColor: '#FF8C00',
                      borderCapStyle: 'butt',
                      borderDash: [],
                      borderDashOffset: 0.0,
                      borderJoinStyle: 'miter',
                      pointBorderColor: '#FF8C00',
                      pointBackgroundColor: '#fff',
                      pointBorderWidth: 1,
                      pointHoverRadius: 5,
                      pointHoverBackgroundColor: '#FF8C00',
                      pointHoverBorderColor: '#FF8C00',
                      pointHoverBorderWidth: 10,
                      pointRadius: 1,
                      pointHitRadius: 10,
                      data: this.state.dataset
                    },
                   
                    {
                        label: 'Recovered',
                        fill: false,
                        type:'bar',
                        lineTension: 3,
                        backgroundColor: 'darkgreen',
                        borderColor: 'darkgreen',
                        borderCapStyle: 'butt',
                        borderDash: [],
                        borderDashOffset: 1,
                        borderJoinStyle: 'miter',
                        pointBorderColor: 'darkgreen',
                        pointBackgroundColor: '#fff',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: 'darkgreen',
                        pointHoverBorderColor: 'darkgreen',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: this.state.recovered
                      }
                  ]
               }}
               options={{
                   responsive:true,
                   maintainAspectRatio:false
               }}
               height={400}
           />
           }
        </div>);
    }
}
export default Timeline;